<template>
  <v-layout>
    <v-container>
      <v-row>
        <v-col>
          <add-office v-on:getVal="getVal" />
        </v-col>
      </v-row>
      <center>
        <v-img
          v-if="!isGetData && !listOfOffice.length"
          class="image-not-fond"
          contain
          :src="'/nodata.png'"
        >
        </v-img>
        <v-progress-circular
          id="progress"
          v-if="isGetData"
          :size="70"
          width="7"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </center>
      <v-row>
        <card-component
          v-on:getVal="getVal"
          v-for="(dataOffice, index) in listOfOffice"
          :key="index"
          :data="dataOffice"
        />
      </v-row>
    </v-container>
  </v-layout>
</template>

<script>
import { officeController } from "../../components/offices/office";

export default {
  mounted() {
    this.getOffices();
  },
  methods: {
    getVal() {
      this.getOffices();
    },

    getOffices: async function () {
      this.listOfOffice = [];
      this.isGetData = true;
      var data = await officeController.getOffices();
      this.isGetData = false;
      this.listOfOffice = data;
    },
  },
  data() {
    return {
      isGetData: false,
      mainUrl: this.$store.state.mainUrl,
      listOfOffice: [],
    };
  },

  components: {
    "card-component": () => import("../../components/offices/Card.vue"),
    "add-office": () => import("../../components/offices/addOffice.vue"),
  },
};
</script>

<style>
#progress {
  margin-top: 20%;
}
.image-not-fond {
  margin-top: 5%;
  height: 500px;
  width: 500px;
}
</style>